body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: blue;
}

.bomb-box {
  height: calc((100vh / 5) - 1px);
  width: calc((100vw / 10) - 1px);
  font-size: 8pt;
  border-style: solid;
  border-color: black;
  border-width: 0.5px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
}

.blank-box {
  background-color: #c4c3c3;
}

.blank-box-clicked {
  background-color: white;
}

.box-with-number {
  background-color: #7f86c3;
}

.box-with-bomb {
  background-color: white;
  background-image: url(/jenochjones/minesweeper/static/media/bomb.90a69345.png);
  background-size: auto 100%;
  background-repeat: no-repeat;
  background-position: center;
}

.box-with-flag {
  background-color: #c4c3c3;
  background-image: url(/jenochjones/minesweeper/static/media/flag.a821390a.png);
  background-size: auto 100%;
  background-repeat: no-repeat;
  background-position: center;
}

.single-row {
  display: flex;
  flex-direction: row;
}

